@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

*{
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  border-bottom: 2px solid #f2f2f2;
}
.navbar h1{
  color: blue;
  text-align: center;
  padding-left: 36%;
}
.content{
  max-width: 1000px;
  margin:  auto;
  padding: 20px;
}
.card-preview{
  display: inline-block;
  flex-wrap: wrap;
  width:  250px;
  height: 210px;
  position: relative;
  overflow: hidden;
  padding: 10px;
  margin: 20px 20px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  border-top: 3px solid blue;
}
.info-1{
  padding: 10px 0 0 15px;
}
.info-2{
  color: blue;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem 1rem;
  letter-spacing: 0.5px;
  margin-top: 0;
  height: auto;
  padding-top: 20px;
}